import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import PasswordStrengthBar from "react-password-strength-bar";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { registerUser } from "store/auth/slice";

// needed styles
import pageStyles from "./index.module.scss";

// firebase

const RegisterForm = () => {
  const dispatch = useDispatch();
  let navigate = useNavigate();

  const { error, isLoggedIn, user } = useSelector(
    (state) => state.authentication
  );
  useEffect(() => {
    if (error) alert(error);
  }, [error]);

  useEffect(() => {
    if (isLoggedIn) navigate("/", { replace: true });
  }, [isLoggedIn, navigate]);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    shouldUseNativeValidation: true,
  });

  const [currentPassword, setCurrentPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [termsAndConditionCheckBox, setTermsAndConditionCheckBox] =
    useState(false);

  //   handler Methods

  // TODO Should complete the login in method
  const submitHandler = async (data) => {
    const {
      password,
      email,
      confirmPassword,
      date_of_birth,
      first_name,
      last_name,
    } = data || {};
    if (password !== confirmPassword) return alert("password does not match");
    dispatch(
      registerUser({ email, password, date_of_birth, first_name, last_name })
    );

    // setCurrentPassword("");
    // reset();
  };

  const handleTermsAndConditionCheckBox = (e) => {
    const { checked } = e?.target || {};
    setTermsAndConditionCheckBox(checked);
  };

  const errorMessage = "هذا الجقل مطلوب ";
  return (
    <form onSubmit={handleSubmit(submitHandler)} className={pageStyles.Form}>
      <Row className={pageStyles.innerFromRow}>
        <Col xs={12} md={6}>
          <label htmlFor="first_name">
            <input
              type="text"
              name="first_name"
              id="first_name"
              placeholder="الاسم الأول"
              {...register("first_name", { required: errorMessage })}
            />
          </label>
          {errors?.first_name && (
            <p className="error-message">{errors?.first_name?.message}</p>
          )}
        </Col>
        <Col xs={12} md={6}>
          <label htmlFor="last_name">
            <input
              type="text"
              name="last_name"
              id="last_name"
              placeholder="اسم العائلة"
              {...register("last_name", { required: errorMessage })}
            />
          </label>
          {errors?.last_name && (
            <p className="error-message">{errors?.last_name?.message}</p>
          )}
        </Col>
        <Col xs={12}>
          <label htmlFor="email">
            <input
              type="email"
              name="email"
              id="email"
              placeholder="البريد الإلكتروني "
              {...register("email", { required: errorMessage })}
            />
          </label>
          {errors?.email && (
            <p className="error-message">{errors?.email?.message}</p>
          )}
        </Col>
        <Col xs={12}>
          <label htmlFor="password">
            <input
              type={showPassword ? "text" : "password"}
              name="password"
              id="password"
              placeholder="كلمة المرور "
              {...register("password", {
                required: errorMessage,
                minLength: 6,
                onChange: (e) => setCurrentPassword(e.target.value),
              })}
            />
            <label htmlFor="showPassword">
              <input
                type="checkbox"
                name="showPassword"
                id="showPassword"
                onChange={(e) => setShowPassword(!showPassword)}
              />
              إظهار كلمة السر
            </label>
            <div>
              <PasswordStrengthBar
                password={currentPassword}
                shortScoreWord="قوة كلمة المرور"
                scoreWords={["ضعفة", "ضعيفة", "جيدة", "جيدة جدا", "ممتازة"]}
              />
            </div>
          </label>
        </Col>
        <Col xs={12}>
          <label htmlFor="confirmPassword">
            <input
              type={showPassword ? "text" : "password"}
              name="confirmPassword"
              id="confirmPassword"
              placeholder="تأكيد كلمة المرور"
              {...register("confirmPassword", {
                minLength: 6,
                required: errorMessage,
              })}
            />
          </label>
        </Col>
        <Col xs={12}>
          <label htmlFor="date_of_birth">
            <input
              type="date"
              name="date_of_birth"
              id="date_of_birth"
              {...register("date_of_birth", { required: errorMessage })}
            />
          </label>
          {errors?.date_of_birth && (
            <p className="error-message">{errors?.date_of_birth?.message}</p>
          )}
        </Col>
        <Col xs={12}>
          <label htmlFor="remmberMe">
            <input
              type="checkbox"
              name="remmberMe"
              id="remmberMe"
              onChange={handleTermsAndConditionCheckBox}
            />
            أوفق على الشروط والأحكام
          </label>
        </Col>
        <Col xs={12} md={6}>
          <input
            disabled={!termsAndConditionCheckBox}
            type="submit"
            value="تسجيل"
          />
        </Col>
      </Row>
    </form>
  );
};

export default RegisterForm;
