import React from "react";
import { Col, Container, Row } from "react-bootstrap";

// needed styles
import pageStyles from "./index.module.scss";

// needed components
import RegisterForm from "./RegisterForm";
const Index = () => {
  return (
    <div className={pageStyles.body}>
      <Container>
        <Row className={pageStyles.mainRow}>
          <Col xs={12} className={pageStyles.pageHeaders}>
            <h3>انضموا إلى تأشيرة</h3>
            <p>
              انضم إلينا الآن لتتمكن من الاستمتاع بجميع الخدمات التي نقدمها،
              ودون الحاجة إلى زيارة مكتبنا فقط من خلال الموقع يمكنك أن تحصل على
              جميع الخدمات
            </p>
          </Col>
          <Col xs={12}>
            <Row className={pageStyles.formRow}>
              <Col xs={{ order: 2, span: 12 }} md={{ order: 1, span: 7 }}>
                <RegisterForm />
              </Col>
              <Col
                className={pageStyles.otherOptions}
                xs={{ order: 1, span: 12 }}
                md={{ order: 2, span: 5 }}
              >
                <img
                  src={"https://source.unsplash.com/random?holding"}
                  alt="img_found"
                />
                <div>
                  <h3>غيروا من نظرتكم للعالم</h3>
                  <p>
                    لن يكلفكم الانضمام شيئا، ما عليكم سوى إكمال نموذج الطلب هذا
                    والبدء بالتمتع بالامتيازات.
                  </p>
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Index;
